@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400;600;700;900&display=swap');
/**hover**/
:root{
  --hover-sky-blue:#63ACDF;
  --fw-100:100;
  --fw-300:300;
  --fw-400:400;
  --fw-700:700;
  --padding-block-components:10rem;

  @media (max-width: 9928px){
    --padding-block-components:5rem;
  }
  body::-webkit-scrollbar {
    width: 23px;   
 
  }
  
  body::-webkit-scrollbar-track {
    background:  linear-gradient(0deg, rgb(255, 0, 47) 0%, rgb(0, 165, 211) 100%);
    margin-block: 0.5rem;
  }
  
  body::-webkit-scrollbar-thumb {
    background: transparent; 
    box-shadow: 0px 0px 0px 100000vh white;
    border-radius: 20px;
    border: .2rem solid white;
  }
}
button:hover{
cursor: pointer;
}
p{
  font-family: 'Source Sans Pro', sans-serif;
  @media (max-width: 768px){
    font-size: 0.9rem;
}

}
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Big+Shoulders+Display:wght@100;300;400;700;900&family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');
.navbar{
  box-shadow:0px 0px 60px -16px black;
  *{
    font-family: 'Source Sans Pro', sans-serif;
font-size: 1.6rem;
&:hover{
  color: var(--hover-sky-blue);
}
  }
}
h1,h2,h3,h4,h5,span,div{
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 1.8rem;
}
body{
  line-height: 1.5rem;
}
*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  color: #100e0e;
  transition: 300ms;
  scroll-behavior: smooth;
}
a.nav-link{
  font-size: 1.3rem;
  text-transform: uppercase;
  font-weight: 600;
  color: #100e0e;
  letter-spacing: 2px;
  width:100%;
text-align: center;
padding: 1rem;
margin: 0px;
&:hover{
  color: #F50059;
}

}
.navbar-brand {
  text-transform: uppercase;
  font-weight: 700;
  display: flex;
  align-items: center;
  p{
    font-size: 1.3rem;
    margin-bottom: 0rem;
    letter-spacing: 2px;
    font-family: var(--ff-navbar);
    @media (max-width: 991.98px){
    	font-size: 1.3rem;
    }
&:hover{
  color: var(--hover-sky-blue);
}

  }
  img{
    width: 3rem;
    margin-right: 1rem;
  }
}
.navbar-nav{
  gap: 2.5rem;
}

@media (max-width: 992px){
/***/
.navbar-toggler{
  border: 0;
  box-shadow: none;
  &:focus{
    border: 0;
  }
  
}


.navbar-collapse {

padding-block: 4rem;
}
a.nav-link{

  border-top: 1px solid ;
  border-bottom: 1px solid ;
}
}