.About__section{
    padding-block:var(--padding-block-components);
    max-width: 120rem;
    margin: auto;
    width: 92%;

    /*containers*/
    &-header{
        display: grid;
        justify-items: center;
        margin-inline: 3rem;
        font-size: 1.5rem;
        text-align: center;
        margin-bottom: 8rem;
        @media (max-width: 780px){
        	margin-bottom: 6rem;
        }
    }
    &-content{
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 5rem;
        margin-inline:3rem ;
        h1{
            margin-bottom: 3rem;
        }
        @media (max-width: 992px){
            grid-template-columns: 1fr;
            margin-inline:1rem ;
        }
    }
    /*childs head*/
    &-sub{
        color: grey;
        letter-spacing: 3px;
        max-width: 50rem;
        
    }
   /*childs main*/
    &-main{

        & p{

            letter-spacing: 1px;
color: grey;
margin-bottom: 1rem;
font-size: 1.3rem;

& a{
color:var(--hover-sky-blue);
text-decoration: none;
font-weight: 600;
}
}
& h1{
    margin-bottom: 3rem;
}
    }
    &-skills-skills{
display: flex;
flex-wrap: wrap;
font-size: 5rem;
gap: 1.5rem;
@media (max-width: 992px){
    gap: .8rem;
    justify-content: space-evenly;
}
    }

}